.modal-backdrop {
  position: fixed;
  top: 0;
  z-index: 15;
  width: 100vw;
  height: 100vh;
  background-color: hsla(0, 0%, 0%, 0.4);
  transition: visibility 300ms ease-in, opacity 300ms ease-in;
}

.film-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 999;
  width: 280px;
  padding: 15px 20px 40px 20px;
  background-color: #ffffff;
  transform: translate(-50%, -50%) scale(1);
  transition: transform 500ms;

  @media screen and (max-width: 767px) {
    max-height: calc(100vh - 100px);
    overflow-y: auto;
    overflow-x: hidden;
  }

  @media screen and (min-width: 768px) {
    display: flex;
    width: 704px;
    height: 552px;
    padding: 40px 72px 40px 36px;
  }

  @media screen and (min-width: 1024px) {
    width: 814px;
    height: 558px;
    padding: 40px 16px;
  }
}

.film-modal-poster-wrap {
  position: relative;
  margin-bottom: 20px;

  @media screen and (min-width: 768px) {
    margin-right: 32px;
    margin-bottom: 0px;
  }

  @media screen and (min-width: 1024px) {
    margin-right: 16px;
  }
}

.film-modal-poster-img {
  width: 240px;
  height: 357px;
  border-radius: 5px;
  z-index: 1001;

  @media screen and (min-width: 768px) {
    width: 264px;
    height: 374px;
  }

  @media screen and (min-width: 1024px) {
    width: 396px;
    height: 478px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.film-modal-item-title {
  min-width: 50%;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  color: #8c8c8c;

  @media screen and (min-width: 768px) {
    min-width: 40%;
  }

  @media screen and (min-width: 1024px) {
    min-width: 30%;
  }
}

.film-modal-close-button {
  position: absolute;
  top: 8px;
  right: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  outline: none;

  @media screen and (max-width: 767px) {
    position: static;
    transform: translateX(225px) translateY(-10px);
  }

  &:hover .film-modal-close-icon,
  &:focus .film-modal-close-icon {
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
    stroke: #ff6b08;
  }
}

.film-modal-info-wrap {
  width: 100%;
}

.film-modal-item {
  display: flex;

  &:not(:last-child) {
    margin-bottom: 8px;
  }
}

.film-modal-title {
  margin-bottom: 20px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;

  @media screen and (min-width: 1024px) {
    font-size: 30px;
    line-height: 35px;
  }

}

.film-modal-item-description {
  font-weight: 500;
  font-size: 12px;
  line-height: 20px;

  text-transform: capitalize;
}

.film-modal-item-vote {
  padding: 1px 10px;
  border-radius: 5px;
  background-color: #f7f7f7;

  &.color {
    background-color: #ff6b08;
    color: #ffffff;
  }
}

.film-modal-item-original-title {
  text-transform: uppercase;
  line-height: 1.33;
}

.film-modal-about {

  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  margin-top: 20px;
  margin-bottom: 8px;

}

.film-modal-description {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;

  @media screen and (max-width: 2560px) {
    height: inherit;
    overflow-y: auto;
  }
}

.film-modal-btn-wrap {
  display: flex;
  margin-top: 20px;

  @media screen and (min-width: 1024px) {
    margin-left: auto;

  }
}

.film-modal-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0px 10px;
  width: 110px;
  height: 44px;
  cursor: pointer;
  font-family: inherit;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-transform: uppercase;
  background-color: initial;
  border: 1px solid #000000;
  border-radius: 5px;
  transition-property: background-color, color, border;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 768px) {
    width: 125px;
  }

  @media screen and (min-width: 1024px) {
    width: 136px;
  }

  &:hover,
  &:focus {
    background-color: #ff6b08;
    color: #ffffff;
    border: 1px solid #ff6b08;
  }
}

.film-modal-btn-watched {
  margin-right: 20px;
}
