.darkmode-toggle {
  height: 30px;
  width: 70px;
  cursor: pointer;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 100px;
  justify-content: space-around;
  align-items: center;
  display: flex;
  position: relative;
  box-shadow: 0 5px 10px rgba(0, 0, 0, .1);
}

@media screen and (max-width: 767px) {
  .darkmode-toggle {
    display: flex;
    position: absolute;
    top: 6px;
    right: .2%;
  }
}

@media screen and (min-width: 768px) {
  .darkmode-toggle {
    display: flex;
    position: absolute;
    top: 6px;
    right: 1%;
  }
}

.darkmode-toggle:before {
  content: "";
  height: 23px;
  width: 23px;
  z-index: 1;
  background: #ff6b08;
  border-radius: 50%;
  transition: all .3s ease-in-out;
  position: absolute;
  top: 50%;
  left: 43px;
  transform: translateY(-50%);
}

.darkmode-toggle.active:before {
  background-color: #18191a;
  left: calc(100% - 65px);
}

.darkmode-icon {
  color: #ff6b08;
}

