.skeleton {
  width: calc(100vw - 40px);
  height: calc(142.143vw - 56.8571px);
  border-radius: 5px;
  margin-bottom: 10px;
  animation: skeleton-loading 1s linear infinite alternate;
}

.skeleton__text {
  width: 100%;
  height: 10px;
  border-radius: .125rem;
  animation: skeleton-loading 1s linear infinite alternate;
  display: block;
}

@keyframes skeleton-loading {
  0% {
    background-color: #a3b8c2;
  }

  100% {
    background-color: #f0f3f5;
  }
}

