.Pagination {
  justify-content: center;
  align-items: center;
  gap: 16px;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  display: none;
}

.Pagination__additionalItem {
  cursor: pointer;
  flex-shrink: 0;
  flex-basis: 14px;
  justify-content: center;
  align-items: center;
  transition: transform 1s linear, background-color .3s ease-in, visibility .3s ease-in, opacity .3s ease-in;
  display: flex;
}

.Pagination__additionalItem:hover, .Pagination__additionalItemfocus {
  color: #ff6b08;
  transform: scale(1.5);
}

.Pagination__containerGrid {
  width: 160px;
  overflow: hidden;
}

.Pagination__grid {
  align-items: center;
  gap: 16px;
  transition: transform .3s ease-in;
  display: flex;
  transform: translateX(0);
}

.Pagination__item {
  cursor: pointer;
  flex-shrink: 0;
  flex-basis: 14px;
  justify-content: center;
  align-items: center;
  transition: transform .3s ease-in, color .3s ease-in;
  display: flex;
}

.Pagination__item:hover, .Pagination__itemfocus {
  color: #ff6b08;
  transform: scale(1.5);
}

.Pagination__item--active {
  height: 40px;
  color: #fff;
  background-color: #ff6b08;
  border-radius: 5px;
  flex-basis: 40px;
}

.Pagination__item--active:hover, .Pagination__item--active focus {
  color: #fff;
  background-color: #000;
  transform: scale(1);
}

.Pagination__arrow {
  width: 40px;
  height: 40px;
  cursor: pointer;
  background: #f7f7f7;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  transition: transform .7s linear, background-color .3s ease-in, visibility .3s ease-in, opacity .3s ease-in;
  display: flex;
}

.Pagination__arrow:hover, .Pagination__arrow :focus {
  color: #fff;
  background-color: #ff6b08;
  transform: scale(1.1);
}

.Pagination__icon {
  height: 16px;
  width: 16px;
  fill: currentColor;
}

.Pagination__icon--right {
  transform: rotate(180deg);
}

